<template>
  <div class="flastcon">
      <img @click="getQRCode()" class="imgbox imgboxdd" :src="shareimg" alt="">
	 <!-- 二维码 -->
	 <div class="sharebgzhao" v-show="erweiamstate" @click="erweiamstate=false"></div>
	 <div class="erwiemabg" v-show="erweiamstate">
	 	<div style="width: 200px;margin-bottom: 40px;height: 260px;background-color:#fff;border-radius:20px;overflow: hidden;padding: 20px;">
	 		<vue-qr :logoSrc="imageUrl" :margin="10" :text="QRCodeMsg" :size="200"></vue-qr>
	 		<div style="font-size: 14px;line-height: 60px;color: #000;">长按保存二维码进行分享</div>
	 	</div>
	 	<van-icon size="35" name="close" @click="erweiamstate=false" />
	 </div>
  </div>
</template>

<script>
	import VueQr from 'vue-qr'//二维码生成
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
      components: {VueQr},
	  data() {
	    return {
			shareimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/ewm.png',//分享logo
			erweiamstate:false,
			QRCodeMsg: "", //生成二维码信息
			imgfx:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/YXTTWM.png',
			imageUrl:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/ewm.png',//默认二维码中间图片
	    };
	  },
	  props:['dataconlist'],
	  methods: {
		//生成分享二维码
		getQRCode(){
			this.imageUrl = this.dataconlist.img[0].url;
			this.QRCodeMsg = 'https://zht.insooner.com/INContrarian/?urlmemId='+this.dataconlist.mem_id;//生成的二维码为URL地址
			this.erweiamstate = true;
		},
	  },
	  mounted() {
	  }
	};
</script>

<style scoped>
  .flastcon{
    position:fixed;
    bottom:120px;
    right: 24px;
    width: 54px;
    z-index:999;
  }
  .imgbox{
    width: 54px;
    height: 54px;
  }
  .sharebg{
	  background-color: rgba(0,0,0,0.5);
	  width: 100%;
	  height: 100%;
	  position: fixed;
	  top: 0;
	  left: 0;
	  z-index: 99;
  }
  .imgboxdd{
  	  -webkit-animation: seconddiv 2s infinite linear;
  	  background: transparent;	
  }
  
  @keyframes seconddiv{
	  0% {transform: scale(1,1);}
	  10% {transform: scale(1,1);}
	  25% {transform: scale(1.2,1.2);}
	  50% {transform: scale(1,1);}
	  70% {transform: scale(1.2,1.2);}
	  100% {transform: scale(1,1);}
  }
  /* 遮罩 */
  .sharebgzhao{
  	  background-color: rgba(0,0,0,0.5);
  	  width: 100%;
  	  height: 100%;
  	  position: fixed;
  	  top: 0;
  	  left: 0;
  }
  /* 二维码 */
  .erwiemabg{
  	position: fixed;
  	width: 240px;
  	z-index: 99;
  	top: 20%;
  	left: 50%;
  	margin-left: -120px;
  	color: #fff;
  }
</style>



