<template>
	<div class="INindex">
		<!-- 头部搜索 -->
		<div class="usercon">
			<div class="userconlogo">
				<img v-lazy="img">
			</div>
			<div class="userconname">
				{{cname}}
			</div>
			<div class="usercontext" @click="inuserdata()">完善资料</div>
		</div>
		<van-tabs v-model="active" :border="false" sticky line-width="20px">
		  <div style="width: 100%;height:10px;"></div>
		  <van-tab title="全部课程">
			  <div v-if="JGCLASS.length==0" style="line-height: 40px;font-size: 12px;color: #333;">暂无课程，请尽快发布课程哦~</div>
			    <div class="briefJJimg" v-for="(item,index) in JGCLASS" :key="index" >
			    	<van-card :thumb="item.img" @click-thumb="detJG()">
			    	  <div slot="tags" style="position: relative;">
			    		<div class="userrem" @click="remuser(item.id)">删除</div>
			    	    <div class="briefJJtit">{{item.name}}</div>
			    		<div class="briefJJtitru" style="color: #999999;">{{item.title}}</div>
			    	  </div>
			    	</van-card>
			    </div>
				<van-button color="#c85e55" style="margin-bottom: 20px;" round block type="info" @click="toRelease">发布课程</van-button>
		  </van-tab>
		  <van-tab title="我的订单">
			  <van-list
			    v-model="loading"
			    :finished="finished"
			    finished-text="没有更多了"
			    @load="onLoad"
			  >
				  <div class="orderst"  v-for="(item,index) in list" :key="index">
					  <div class="orderstbq">
						  <span>{{item.ccname}}</span>
					  </div>
					  <div class="orderstlogo">
						  <img v-lazy="item.img">
					  </div>
					  <div class="orderstcon">
						  <span>{{item.name}}</span>
						  <span>&nbsp;&nbsp;&nbsp;&nbsp;{{item.phone}}</span>
						  <span style="float: right;color: #f2cf2b;">{{item.age}}岁</span>
					  </div>
					  <div class="ordersttime">报名时间：{{item.create_time}}</div>
					  <van-divider />
				  </div>
			  </van-list>
		  </van-tab>
		</van-tabs>
		<!-- 浮动框 -->
		<flastbox v-bind:dataconlist="dataconlist"></flastbox>
	</div>
</template>

<script>
	import flastbox from "./compagejs/flastbox.vue"; //分享
	import {
		Toast,Dialog 
	} from 'vant';	
	export default {
		components: {
			flastbox,
		},	
		data() {
			return {
				active: 0,
				img:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/tx.png',
				list: [],
			    loading: false,
			    finished: false,
				cname:'',
				JGCLASS:[],
				gjstate:0,
				page:1,
				dataconlist:''
			}
		},
		methods: {
			//资料
			inuserdata(){
				this.$router.push({path:'/userdata'});
			},
			//发布
			toRelease(){
				if (this.gjstate == 1) {
					this.$router.push({path:'/Release'});
				} else{
					Dialog.confirm({
					  title: '温馨提示',
					  message: '您当前还没有完善资料，是否去完善？'
					}).then(() => {
					  // on confirm
					  this.$router.push({path:'/userdata'});
					}).catch(() => {
					  // on cancel
					});
				};
			},
			// 个人中心
			userurl(){
				this.$router.push({path:'/user'});
			},
			remuser(e){
				Dialog.confirm({
				  title: '删除课程',
				  message: '是否删除当前选择的课程'
				}).then(() => {
				  // on confirm
				  let that = this;
				  let data={
				  	id:e
				  };
				  that.axios.post('v1/delContrarianbackCourse',that.qs.stringify(data)).then((response)=>{
				      // ...
				  	if (response.data.code =='1000') {
				  		Toast(response.data.msg);
						that._USERCLASS();
				  	} else{
				  		Toast(response.data.msg);
						that.JGCLASS=[];
				  	}
				  })
				}).catch(() => {
				  // on cancel
				});  
			},
			// 下拉加载
			onLoad() {
			      // 异步更新数据
			      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
			      setTimeout(() => {
					this._USERLIST();
			        // 加载状态结束
			        this.loading = false;
			      }, 1000);
			},
			//用户数据请求
			_USERLIST(){
				let that = this;
				let data={
					mem_id:this.$uerstore.store.state.INContMechUserId,
					token:this.$uerstore.store.state._INTOKEN,
					page:that.page
				};
				that.axios.post('v1/getContrarianBackorder',that.qs.stringify(data)).then((response)=>{
				    // ...
					console.log(response.data);
					if (response.data.code =='1000') {
						that.list = that.list.concat(response.data.data);//追加
						that.page++
					} else{
						Toast(response.data.msg);
						this.finished = true;
					}
				})
			},
			//获取机构信息
			_USERDATA(){
				let that = this;
				let data={
					mem_id:this.$uerstore.store.state.INContMechUserId,
					token:this.$uerstore.store.state._INTOKEN
				};
				that.axios.post('v1/getContrarianbakcMember',that.qs.stringify(data)).then((response)=>{
				    // ...
					console.log(response.data.data);
					if (response.data.code =='1000') {
						that.dataconlist = response.data.data;
						that.cname = response.data.data.cname;
						that.gjstate = response.data.data.is_verification;
						if (response.data.data.logo.length!=0) {
							this.img = response.data.data.logo[0].url;
						};
					} else{
						// Toast(response.data.msg);
					}
				})
			},
			//全部课程
			_USERCLASS(){
				let that = this;
				let data={
					mem_id:this.$uerstore.store.state.INContMechUserId,
					token:this.$uerstore.store.state._INTOKEN
				};
				that.axios.post('v1/getContrarianbackCourse ',that.qs.stringify(data)).then((response)=>{
				    // ...
					if (response.data.code =='1000') {
						that.JGCLASS = response.data.data;
						console.log(that.JGCLASS);
					} else{
						// Toast(response.data.msg);
						that.JGCLASS = [];
					}
				})
			}
		},
		mounted() {
			// token验证用户是否登录
			let _USERIDTOKEN = this.$uerstore.store.state._INTOKEN;
			if(_USERIDTOKEN=="" || _USERIDTOKEN==undefined || _USERIDTOKEN==null ){
				this.$router.push({path:'/Login'});
			};
			// 获取机构信息
			this._USERDATA();
			this._USERCLASS();
			// 禁止返回
			if (window.history && window.history.pushState) {
				// 向历史记录中插入了当前页
				history.pushState(null, null, document.URL);
				window.addEventListener('popstate', this.goBack, false);
			}
		},
		destroyed () {
			//页面销毁返回销毁
		    window.removeEventListener('popstate', this.goBack, false);
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	/* 我的订单 */
	.orderst{
		width: 100%;
		text-align: left;
		overflow: hidden;
	}
	.ordersttime{
		font-size: 12px;
		color: #666;
		margin-top: 20px;
	}
	.orderstbq{
		width: 100%;
		height: 26px;
		margin-bottom: 12px;
	}
	.orderstlogo{
		width: 35px;
		height: 35px;
		border-radius: 50%;
		overflow: hidden;
		float: left;
	}
	.orderstcon{
		width: calc(100% - 50px);
		float: left;
		margin-left: 15px;
		overflow: hidden;
		line-height: 35px;
		font-size: 14px;
	}
	.orderstlogo img{
		width: 100%;
		display: block;
	}
	.orderstbq span{
		background-color: #f2cf2b;
		font-size: 12px;
		padding:6px 12px;
		color: #FFF;
		border-radius:0 20px 20px 0;
		float: left;
	}
	.userrem{
		position: absolute;
		right: 0;
		top: 0;
		font-size: 14px;
		color: #E14901;
		line-height: 27px;
	}
	/* 列表 */
	/* 简介 */
	.briefJJimg{
		text-align: left;
		margin-bottom: 20px;
	}
	.briefJJtitru{
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		font-size: 14px;
	}
	.briefJJtit{
		width: calc(100% - 50px);
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
	}
	.briefJJ{
		padding: 20px 14px;
		background-color: #fff;
		margin-bottom: 10px;
		text-align: left;
	}
	.briefJJTit{
		height: 44px;
		text-align: left;
		line-height: 44px;
		color:rgba(51,51,51,1);
		font-weight: bold;
	}
	.briefJJTit img{
		width: 22px;
		height: 22px;
		float: left;
		margin: 11px 10px 0 0;
	}
	.briefJJTEXT{
		font-size: 14px;
		color: #736E6B;
		text-align: left;
		margin-bottom: 60px;
		padding-bottom:20px;
	}
	.detnav{
		font-size:16px;
		font-weight: bold;
		text-align: left;
		position: relative;
		padding-left:12px;
		color: #333333;
		margin-bottom:20px;
	}
	.detnav::after{
		position: absolute;
		top: 0;
		left: 0;
		width: 4px;
		height: 21px;
		content: '';
		background:linear-gradient(313deg,#e08078 0%,#c85e55 100%);
		border-radius:10px;
	}
	.detnavimg{
		width: 100%;
		max-height:180px;
		overflow: hidden;
		border-radius: 12px;
		margin-bottom: 12px;
	}
	.detnavimg img{
		width: 100%;
		display: block;
	}
	/* 上半部分 */
	.usernav{
		margin: 15px 0;
	}
	.usernav_st div{
		font-size: 16px;
		color: #333333;
	}
	.usernav_st p{
		margin: 6px 0;
		color: #999;
		font-size: 14px;
	}
	.INindex{
		width:calc(100% - 30px);
		height:calc(100% - 30px);
		padding:15px;
	}
	.usercon{
		width: 100%;
		overflow: hidden;
	}
	.userconlogo{
		float: left;
		width: 40px;
		height: 40px;
		margin-right: 15px;
	}
	.userconlogo img{
		width: 100%;
		display: block;
	}
	.userconname{
		font-size:14px;
		color: #333333;
		width: calc(100% - 150px);
		float: left;
		line-height: 40px;
		text-align: left;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.usercontext{
		float: right;
		font-size: 12px;
		padding: 2px 12px;
		border-radius: 12px;
		line-height: 40px;
	}
</style>




