<template>
	<div class="Inheader">
		<div class="Inheaderimg" @click="toureluser()">
			<img v-lazy="yyimg">
		</div>
		<h4>机构注册</h4>
		<div class="Ininput">
		  <van-field
			v-model="data.cname"
			class="Ininputst"
			clearable
			placeholder="机构名称"
		  />
		  <!-- 省市区选择 -->
		  <van-field
		    readonly
		    clickable
			style="margin-bottom: 10px;"
		    label="城市选择"
		    :value="value"
		    placeholder="选择城市"
		    @click="showPicker = true"
		  />
		  <van-popup v-model="showPicker" position="bottom">
		    <van-area :area-list="areaList" @confirm="onConfirm" @cancel="cancel" value="330000" />
		  </van-popup>
		  
		  <van-field
			v-model="data.address"
			class="Ininputst"
			clearable
			placeholder="详细地址"
		  />
		  <van-field
			v-model="data.nickname"
			class="Ininputst"
			clearable
			placeholder="联系人"
		  />
		  <van-field
			v-model="data.phone"
			class="Ininputst"
			clearable
			placeholder="联系电话">
			<van-button class="Inbust" v-show="show" slot="button" size="small" type="primary" @click="oncode()">发送验证码</van-button>
			<van-button class="Inbust" v-show="!show" slot="button" size="small" type="primary">{{count}}</van-button>
		  </van-field>
		  <van-field
			v-model="data.code"
			class="Ininputst"
			clearable
			placeholder="验证码"
		  />
		  <van-field
			v-model="data.password"
			class="Ininputst"
			clearable
			type="password"
			placeholder="设置密码"
		  />
		</div>
		<div style="width: 100%;overflow: hidden;">
			<div class="xiye"> <van-checkbox v-model="checked" style="float: left;margin-right:10px;" checked-color="#c85e55" shape="square" icon-size="18px"></van-checkbox>同意 <span @click="xieyitext(1)">《逆行者活动协议》</span>和<span @click="xieyitext(2)">《未来社区机构联盟协议》</span> </div>
		</div>
		<van-button round  type="primary" color="#c85e55" block @click="toindex()" >确认注册</van-button>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';	
	import Area from '../../USERJS/Area.js'
	export default {
	  data() {
	    return {
		  checked: true,
		  show: true,
		  count: '',
	      yyimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/yy.png',
		  value: '',
		  showPicker: false,
		  timer: null,
		  areaList: Area,//省市区内容
		  data:{
			  cname:'',
			  province :'',
			  city :'',
			  area :'',
			  address:'',
			  nickname :'',
			  phone :'',
			  code :'',
			  password:''
		  }
	    };
	  },
	  methods: {
		xieyitext(e){
			if(e==1){
				this.$router.push({path:'/Protocolone'});
			}else{
				this.$router.push({path:'/Protocolwto'});
			}
		},
			// 提交接口
	    toindex(){
			if (this.data.cname=='') {
				return Toast("请输入用户名!");  
			};
			if (this.data.province=='') {
				return Toast("请选择省市区!");  
			};
			if (this.data.address=='') {
				return Toast("请填写详细地址!");  
			};
			if (this.data.nickname=='') {
				return Toast("请填写联系人!");  
			};
			if (!(/^1[3456789]\d{9}$/.test(this.data.phone))) {
				return Toast("请手机号填写有误!");  
			};
			if (this.data.code=='') {
				return Toast("请输入验证码!");  
			};
			if (this.data.password=='') {
				return Toast("请输入密码!");  
			};
			// 经纬度转换
			// 详细地址转换经纬度
			let that = this;
			var geocoder, map, marker = null;
			geocoder = new qq.maps.Geocoder();
			var address =this.data.province+this.data.city+this.data.area+this.data.address;         
            geocoder.getLocation(address); //对指定地址进行解析
            //设置服务请求成功的回调函数
            geocoder.setComplete(function(result) {
			   // 提交接口
			   // console.log(result.detail.location.lat);
			   that.data.latitude = result.detail.location.lat;
			   that.data.longitude = result.detail.location.lng;
			   if (that.checked==false) {
			   	 that.data.is_union=2; 
			   } else{
			   	 that.data.is_union=1; 
			   };
			   that.axios.post('v1/setContrarianRegister',that.qs.stringify(that.data)).then((response)=>{
			       // ...
			   	if (response.data.code =='1000') {
			   		Toast("注册成功");
			   		that.$router.push({path:'/Login'});
			   	} else{
			   		Toast(response.data.msg);
			   	}
			   })
			});		
		},
		onConfirm(value) {
			this.value = value[0].name+'>'+value[1].name+'>'+value[2].name;
			this.data.province = value[0].name;
			this.data.city = value[1].name;
			this.data.area = value[2].name;
			this.showPicker = false;
		},
		cancel(){
			this.showPicker = false;
		},
		// 验证码
		//获取验证码
		oncode(){
			if(!(/^1[3456789]\d{9}$/.test(this.data.phone))){
				Toast("手机号码有误，请重填");  
				return false; 
			}else{
				//发送验证码
				const TIME_COUNT = 60;
				 if (!this.timer) {
				   this.count = TIME_COUNT;
				   this.show = false;
				   this.timer = setInterval(() => {
				   if (this.count > 0 && this.count <= TIME_COUNT) {
					 this.count--;
					} else {
					 this.show = true;
					 clearInterval(this.timer);
					 this.timer = null;
					}
				   }, 1000)
				  }	
				this.codeajax();
			}
		},
		//获取验证码
		codeajax(){
			let codees={
				phone:this.data.phone
			};
			this.axios.post('v1/getContrarianedPhoneCode',this.qs.stringify(codees)).then((response)=>{
			    // ...		
				Toast("手机验证码已发送，请注意查收"); 			
			})
		},
		//跳转到家长页面
		toureluser(){
			window.location.href = "https://zht.insooner.com/INContrarian";
		}
	  }
	};
</script>

<style scoped>
	.xiye{
		font-size: 12px;
		margin: 20px 0;
		float: left;
	}
	.xiye span{
		color: #c85e55;
		text-decoration:underline;
	}
	.Inbust{
		color: #666666;
		background-color: inherit;
		border: none;
	}
	.Innav{
		width:calc(100% - 80px);
		padding: 0 40px;
		overflow: hidden;
	}
	.Innav p{
		width: 50%;
		float: left;
		font-size: 12px;
	}
	.Inheader{
		width:calc(100% - 30px);
		padding: 15px;
		height: calc(100% - 30px);
		text-align: left;
	}
	.Inheaderimg{
		width: 100%;
		overflow: hidden;
	}
	.Inheaderimg img{
		width: 100%;
		display: block;
	}
	.Ininput{
		width:calc(100% - 40px);
		padding: 0 20px;
		margin-bottom: 40px;
	}
	.Ininputst{
		background-color: #F8F8F8;
		border-radius: 50px;
		margin-bottom: 10px;
		color: #C4C4C4;
	}
</style>



