<template>
	<div class="Inheader">
		<div class="Inheaderimg" @click="toureluser()">
			<img v-lazy="yyimg">
		</div>
		<h4>修改密码</h4>
		<div class="Ininput">
		  <van-field
			v-model="data.phone"
			class="Ininputst"
			clearable
			placeholder="电话号">
			<van-button class="Inbust" v-show="show"  slot="button" size="small" type="primary" @click="oncode()">发送验证码</van-button>
			<van-button class="Inbust" v-show="!show" slot="button" size="small" type="primary">{{count}}</van-button>
		  </van-field>
		  <van-field
			v-model="data.code"
			class="Ininputst"
			clearable
			placeholder="验证码"
		  />
		  <van-field
			v-model="data.password"
			class="Ininputst"
			clearable
			placeholder="新密码"
			type="password"
		  />
		  <van-field
			v-model="data.new_password"
			class="Ininputst"
			clearable
			placeholder="确认新密码"
			type="password"
		  />
		</div>
		<van-button round  type="primary" color="#c85e55" block @click="toindex()" >确认修改</van-button>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	import Area from '../../USERJS/Area.js'
	export default {
	  data() {
	    return {
		  show: true,
		  count: '',
		  timer: null,
	      yyimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/yy.png',
		  data:{
			 phone:'',
			 code:'',
			 password:''
		  }
	    };
	  },
	  methods: {
	    toindex(){
			if (!(/^1[3456789]\d{9}$/.test(this.data.phone))) {
				return Toast("请手机号填写有误!");  
			};
			if (this.data.code=='') {
				return Toast("请输入验证码!");  
			};
			if (this.data.password=='') {
				return Toast("请输入密码!");  
			};
			if (this.data.password!=this.data.new_password) {
				return Toast("密码确认有误!");  
			};
			let that = this;
			that.axios.post('v1/edContrarianedLoginAccount ',that.qs.stringify(that.data)).then((response)=>{
			    // ...
				if (response.data.code =='1000') {
					Toast("修改密码成功");
					that.$router.push({path:'/Login'});
				} else{
					Toast(response.data.msg);
				}
			})
		},
		//获取验证码
		oncode(){
			if(!(/^1[3456789]\d{9}$/.test(this.data.phone))){
				Toast("手机号码有误，请重填");  
				return false; 
			}else{
				//发送验证码
				const TIME_COUNT = 60;
				 if (!this.timer) {
				   this.count = TIME_COUNT;
				   this.show = false;
				   this.timer = setInterval(() => {
				   if (this.count > 0 && this.count <= TIME_COUNT) {
					 this.count--;
					} else {
					 this.show = true;
					 clearInterval(this.timer);
					 this.timer = null;
					}
				   }, 1000)
				  }	
				this.codeajax();
			}
		},
		//获取验证码
		codeajax(){
			let codees={
				phone:this.data.phone
			};
			this.axios.post('v1/getContrarianedPhoneCode',this.qs.stringify(codees)).then((response)=>{
			    // ...		
				Toast("手机验证码已发送，请注意查收"); 			
			})
		},
		//跳转到家长页面
		toureluser(){
			window.location.href = "https://zht.insooner.com/INContrarian";
		}
	  }
	};
</script>

<style scoped>
	.Inbust{
		color: #666666;
		background-color: inherit;
		border: none;
	}
	.Innav{
		width:calc(100% - 80px);
		padding: 0 40px;
		overflow: hidden;
	}
	.Innav p{
		width: 50%;
		float: left;
		font-size: 12px;
	}
	.Inheader{
		width:calc(100% - 30px);
		padding: 15px;
		height: calc(100% - 30px);
		text-align: left;
	}
	.Inheaderimg{
		width: 100%;
		overflow: hidden;
	}
	.Inheaderimg img{
		width: 100%;
		display: block;
	}
	.Ininput{
		width:calc(100% - 40px);
		padding: 0 20px;
		margin-bottom: 40px;
	}
	.Ininputst{
		background-color: #F8F8F8;
		border-radius: 50px;
		margin-bottom: 10px;
		color: #C4C4C4;
	}
</style>



