<template>
	<div class="userdata">
		<div style="margin:20px 0;">{{GJDATA.cname}}</div>
		<!-- 内容 -->
		<van-form @submit="onSubmit">
		  <!-- 机构LOGO文件上传 -->
		  <van-divider content-position="left">机构LOGO</van-divider>
		  <img :src="logoimg" alt="" id="img" style="width: 80px;height: 80px;display: block;">
		  <a href="javascript:;" class="file">
			上传机构logo
			<input type="file" @change="change($event)">
		  </a>
		  <van-field
		    v-model="GJDATA.nickname"
		    name="nickname"
		    placeholder="机构负责人姓名"
		    :rules="[{ required: true, message: '请填写机构负责人姓名' }]"
		  />
		  <van-field
		    v-model="GJDATA.phone "
		    name="phone"
		    placeholder="联系方式"
		    :rules="[{ required: true, message: '请填写联系方式' }]"
		  />
		 <van-field
		    v-model="GJDATA.remarks"
			name="remarks"
		    rows="2"
		    autosize
		    type="textarea"
		    placeholder="机构简介"
		    show-word-limit
		  />
		  <!-- 个性标签 -->
		 <van-field
		   readonly 
		    placeholder="添加个性标签"
			right-icon="add-o"
			@click-right-icon="addli()"
		  />
		  <van-field
			v-for="(item,index) in GJDATA.label" :key="index"
		    v-model="GJDATA.label[index]"
			@click-right-icon="rem(index)"
		    placeholder="请输入个性标签"
		  	right-icon="close"
		  />
		  <!-- 营业执照文件上传 -->
		  <van-divider content-position="left">营业执照</van-divider>
		  <van-field name="mlicense">
		    <template #input>
		      <van-uploader  v-model="GJDATA.mlicense" :max-count="1" :after-read="afterRead"  />
		    </template>
		  </van-field>
		 <!-- 机构展示图 -->
		 <van-divider content-position="left">机构展示图</van-divider>
		 <van-field name="img" :rules="[{ required: true, message: '请上传机构展示图' }]">
		   <template #input>
		     <van-uploader  v-model="GJDATA.img" :max-count="1" :after-read="afterReadwto"  />
		   </template>
		 </van-field>
		  <div style="margin: 16px;">
		    <van-button color="#c85e55" round block type="info" native-type="submit">提交</van-button>
		  </div>
		</van-form>
		<!-- 图片裁剪 -->
	</div>
</template>

<script>
	import OSS from 'ali-oss';
	import {
		Toast,Dialog 
	} from 'vant';	
	export default {
	  data() {
	    return {
		  GJDATA:[],
		  logoimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/tx.png',
	    };
	  },
	  methods: {
		  change( event ) {
		      let image = document.getElementById('img'); //预览对象
		      this.clip(event, {
		        resultObj : image,
		        aspectRatio : 1
		      });
		  },
		  //上传头像
		  logourl(e){
			  let that = this;
			  let dataLGlogo = {
				  imgBase64:e
			  };
			  that.GJDATA.logo = [];
			  that.axios.post('v1/getimageDoAliyunOss',that.qs.stringify(dataLGlogo)).then((response)=>{
			      // ...
			  	if (response.data.code =='1000') {
			  		console.log(response.data);
					let dataurl = {
						url:response.data.data
					};
					that.GJDATA.logo.push(dataurl);
			  	} else{
			  		Toast('头像上传失败，请重新上传');
					that.logoimg = 'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/tx.png';
			  	}
			  })
		  },
	      onSubmit(values) {
	        console.log('submit', this.GJDATA);
			let that = this;
			that.GJDATA.mem_id = that.$uerstore.store.state.INContMechUserId;
			that.GJDATA.token = that.$uerstore.store.state._INTOKEN;
			that.axios.post('v1/perfectContrarianMember',that.qs.stringify(that.GJDATA)).then((response)=>{
			    // ...
				if (response.data.code =='1000') {
					console.log(response.data);
					Toast(response.data.msg);
					that.$router.push({path:'/'});
				} else{
					Toast(response.data.msg);
				}
			})
	      },
		  // 添加
		  addli(){
			this.GJDATA.label.push('');
		  },
		  // 删除
		  rem(e){
			this.GJDATA.label.splice(e,1);  
		  },
		  //上传营业执照
		  afterRead(file) {
			file.status = 'uploading';
			file.message = '上传中...';
			let that=this;
			this.$axios.get('https://zht.insooner.com/ststoken.php').then(function(res){
				that.getOssToken(res.data.Credentials,file,that.GJDATA.mlicense);
			}).catch(function (error) {
				console.log(error);
			});
		  },
		  afterReadwto(file) {
			file.status = 'uploading';
			file.message = '上传中...';
			let that=this;
			this.$axios.get('https://zht.insooner.com/ststoken.php').then(function(res){
				that.getOssToken(res.data.Credentials,file,that.GJDATA.img);
			}).catch(function (error) {
				console.log(error);
			});
		  },
		  // 阿里云上传公共文件
		  async getOssToken(data,content,urlthis){
		  	let that = this;
		  	// 文件名称
		  	let videoName = 'images/nixingzhe/'+Date.parse(new Date())/1000+content.file.name;
		  	let client = new OSS({
		  		 region: 'oss-cn-hangzhou',
		  		 accessKeyId: data.AccessKeyId,
		  		 accessKeySecret: data.AccessKeySecret,
		  		 stsToken: data.SecurityToken,
		  		 bucket:'yinxuejiaoyu'
		  	 });
		  	 try {
		  		let result = await client.multipartUpload(videoName , content.file, {
		  			progress: async function (p) {//这是上传进度条
					   console.log(p)
		  				if (p==1) {
							// 上传成功
		  					content.status = '';
		  				};
		  			}
		  		 });
		  		return (urlthis[urlthis.length-1] = [result].map(curr =>{//on-change回调的参数
		  			return {
		  				name:Date.parse(new Date())/1000+content.file.name,
		  				url: curr.res.requestUrls[0].split('?')[0],
		  				response:curr.res.requestUrls[0].split('?')[0],
		  			}
		  		})[0]);
		  	} catch (e) {
		  		 console.log('失败',e)
		  		if (e.code === 'ConnectionTimeoutError') {
		  			setTimeout(() => {
		  			  content.status = 'failed';
		  			  content.message = '上传失败';
		  			}, 1000);
		  			throw "上传超时!";
		  		}else{
		  			setTimeout(() => {
		  			  content.status = 'failed';
		  			  content.message = '上传失败';
		  			}, 1000);
		  		}
		  	}
		  },
		  // 上传头像接口
		  
		  
		  //获取机构信息
		  _USERDATA(){
		  	let that = this;
		  	let data={
		  		mem_id:this.$uerstore.store.state.INContMechUserId,
		  		token:this.$uerstore.store.state._INTOKEN
		  	};
		  	that.axios.post('v1/getContrarianbakcMember',that.qs.stringify(data)).then((response)=>{
		  	    // ...
		  		if (response.data.code =='1000') {
		  			that.GJDATA = response.data.data;
					that.logoimg = response.data.data.logo[0].url;
					console.log(that.GJDATA);
		  		} else{
		  			Toast(response.data.msg);
		  		}
		  	})
		  },
	    },
		mounted() {
			this._USERDATA();
		}
	}
</script>

<style scoped>
	.userdata{
		width: 100%;
	}
	.userdatalogo{
		width: 40px;
		height: 40px;
		overflow: hidden;
		border-radius: 50%;
		margin: 20px auto 10px auto;
	}
	.userdatalogo img{
		width: 100%;
		display: block;
	}
	.file {
	    position: relative;
	    display: inline-block;
	    background: #D0EEFF;
	    border: 1px solid #99D3F5;
	    border-radius: 4px;
	    padding: 4px 12px;
	    overflow: hidden;
	    color: #1E88C7;
	    text-decoration: none;
	    text-indent: 0;
	    line-height: 20px;
		margin-top: 20px;
	}
	.file input {
	    position: absolute;
	    right: 0;
	    top: 0;
	    opacity: 0;
		margin: 0;
	}
	.file:hover {
	    background: #AADFFD;
	    border-color: #78C3F3;
	    color: #004974;
	    text-decoration: none;
	}
	/* #img{
		float: left;
	} */
</style>



