<template>
	<div class="Inheader">
		<div class="Inheaderimg" @click="toureluser()">
			<img v-lazy="yyimg">
		</div>
		<h4>机构登录</h4>
		<div class="Ininput">
		  <van-field
			v-model="data.username"
			class="Ininputst"
			clearable
			left-icon="manager"
			placeholder="用户名/账号/手机号"
		  />
		  <van-field
			v-model="data.password"
			class="Ininputst"
			clearable
			left-icon="lock"
			type="password"
			placeholder="请输入您的密码"
		  />
		</div>
		<van-button round  type="primary" color="#c85e55" block @click="toindex()" >登录</van-button>
		<div class="Innav">
			<p style="text-align:left;" @click="toregistered()">账号注册</p>
			<p style="text-align: right;" @click="toPassword()">忘记密码</p>
		</div>
		<!-- 活动说明 -->
		<van-divider>活动说明</van-divider>
		<div style="color: #999;font-size: 14px;">
			<p>爱心机构/商家加入方式</p>
			1，填写活动参与申请表，并认同本次《因学逆行者活动规范》内相关说明要求；<br>
			2，参与机构/商家通过因学平台选择申请参加的活动、并填写递交相关福利，（提供的课程服务/商家优惠必须保障真实有效）；<br>
			3，因学平台收到机构/商家申请信息，会在3个工作日内完成审核，如有疑问请及时与我们400-006-0996联系。</div>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
	  data() {
	    return {
	      yyimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/yy.png',
		  data:{
			username: '',
			password: '',  
		  }
	    };
	  },
	  methods: {
		  // 登录
	    toindex(){
			if (this.data.username=='') {
				return Toast("账号不能为空!");  
			};
			if (this.data.password=='') {
				return Toast("请输入密码!");  
			};
			let that = this;
			that.axios.post('v1/getContrarianLoginAccount',that.qs.stringify(that.data)).then((response)=>{
			    // ...
				if (response.data.code =='1000') {
					Toast("登录成功");
					// console.log(response);
					that.$uerstore.store.commit("INUserId",response.data.data.mem_id);//用户写入
					that.$uerstore.store.commit("INTOKENID",response.data.data.token);//TOEN写入
					that.$router.push({path:'/'});
				} else{
					Toast(response.data.msg);
				}
			})
		},
		//注册
		toregistered(){
			this.$router.push({path:'/registered'});
		},
		//忘记密码
		toPassword(){
			this.$router.push({path:'/Password'});
		},
		//跳转到家长页面
		toureluser(){
			window.location.href = "https://zht.insooner.com/INContrarian";
		}
	  }
	};
</script>

<style scoped>
	.Innav{
		width:calc(100% - 80px);
		padding: 0 40px;
		overflow: hidden;
	}
	.Innav p{
		width: 50%;
		float: left;
		font-size: 12px;
	}
	.Inheader{
		width:calc(100% - 30px);
		padding: 15px;
		height: calc(100% - 30px);
		text-align: left;
	}
	.Inheaderimg{
		width: 100%;
		overflow: hidden;
	}
	.Inheaderimg img{
		width: 100%;
		display: block;
	}
	.Ininput{
		width:calc(100% - 40px);
		padding: 0 20px;
		margin-bottom: 40px;
	}
	.Ininputst{
		background-color: #F8F8F8;
		border-radius: 50px;
		margin-bottom: 10px;
		color: #C4C4C4;
	}
</style>



