import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'//首页
import Login from '@/components/Login/Login'//登录
import registered from '@/components/Login/registered'//注册
import Password from '@/components/Login/Password'//忘记密码
import userdata from '@/components/userdata/userdata'//用户资料
import Release from '@/components/userdata/Release'//用户资料编辑
import Protocolone from '@/components/Protocoltext/Protocolone'//用户资料
import Protocolwto from '@/components/Protocoltext/Protocolwto'//用户资料
Vue.use(Router)

export default new Router({
	mode: 'history',
	base: '/INContMech/',
    routes: [
    {
      path: '/',
      name: 'index',
      component: index,
	    meta:{index:0}
    },
	{
	  path: '/Login',
	  name: 'Login',
	  component: Login,
	    meta:{index:1}
	},
	{
	  path: '/registered',
	  name: 'registered',
	  component: registered,
	    meta:{index:2}
	},
	{
	  path: '/Password',
	  name: 'Password',
	  component: Password,
	    meta:{index:2}
	},
	{
	  path: '/userdata',
	  name: 'userdata',
	  component: userdata,
	    meta:{index:3}
	},
	{
	  path: '/Release',
	  name: 'Release',
	  component: Release,
	    meta:{index:4}
	},
	{
	  path: '/Protocolone',
	  name: 'Protocolone',
	  component: Protocolone,
	    meta:{index:5}
	},
	{
	  path: '/Protocolwto',
	  name: 'Protocolwto',
	  component: Protocolwto,
	    meta:{index:5}
	},
  ]
})



