<template>
  <div id="app">
	   <transition :name="transitionName"> 
	      <!-- <keep-alive> -->
             <router-view/><!-- 缓存页面 -->
		  <!-- </keep-alive> -->
	   </transition>   
  </div>
</template>

<script>
import {commonShare, shareTitle, shareUrl, shareImg, shareDesc} from "./USERJS/share";
//引入微信
import wx from 'weixin-js-sdk'
export default {
	data() {
	  return {
		transitionName:''
	  };
	},
	created () {
		//授权
		// token验证用户是否登录
		let _USERIDTOKEN = this.$uerstore.store.state._INTOKEN;
		if(_USERIDTOKEN=="" || _USERIDTOKEN==undefined || _USERIDTOKEN==null ){
			this.$router.push({path:'/Login'});
		};
		//授权
	    // this.getCode();
		//定位
		// this.getMyLocation();
	},
  methods:{
		//授权登录
		getCode () { // 非静默授权，第一次有弹框
			if (localStorage.getItem('INcodeuid')==undefined || localStorage.getItem('INcodeuid')==null ||localStorage.getItem('INcodeuid')=='') {
				const code = this.$uerstore.getUrlParam("code") // 截取路径中的code，如果没有就去微信授权，如果已经获取到了就直接传code给后台获取openId
				const local = window.location.href
				if (code == null || code === '') {
					window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + 'wxf47f6fd95e94366e' + '&redirect_uri=' + encodeURIComponent(local) + '&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect'
				} else {
					this.getOpenId(code) //把code传给后台获取用户信息
				}
			}else{
				//验证是否是有效登录
				let YZUID = {
					id:localStorage.getItem('INcodeuid')
				};
				this.axios.post('verId',this.qs.stringify(YZUID)).then((response)=>{
				    // ...	
					//授权失败重新访问页面
					if (response.data.code!=1000) {
						//重新访问页面
						localStorage.removeItem('INcodeuid');
						window.location.href = 'https://yxhd.insooner.com/dist';
					}else{
						//直接跳转指定页面
						this.INpagethis();
						commonShare('因学周年庆 百万豪礼大放送','https://yxhd.insooner.com/dist?urluid='+localStorage.getItem('INcodeuid'),'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/80.jpg','价值4800元超值大礼包，限时免费领！');
					}	
				})
			}
		},
		getOpenId (code) { // 通过code获取 openId等用户信息，/api/user/wechat/login 为后台接口
			let _this = this
			let thisdatacode = {
				code:code
			};
			this.axios.post('vx',this.qs.stringify(thisdatacode)).then((response)=>{
			    // ...	
				//授权失败重新访问页面
				if (response.data.code!=1000) {
					//重新访问页面
					window.location.href = 'https://yxhd.insooner.com/dist';
				}else{
					localStorage.setItem('INcodeuid',response.data.data.id);
					//直接跳转指定页面
					this.INpagethis();
					_this.$uerstore.store.commit("GZHcode",response.data.data.type);//公众号
					console.log(response);
					commonShare('因学周年庆 百万豪礼大放送','https://yxhd.insooner.com/dist?urluid='+response.data.data.id,'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/80.jpg','价值4800元超值大礼包，限时免费领！');
				}	
			});
		},
		//获取微信的授权信息定位
		getMyLocation() {
			const that =this;
			//经纬度转换
			const latng = this.$route.query.latng;
			const addr = this.$route.query.city;//地图选点返回内容
			// console.log(this.$route.query);
			if (latng==null || latng=='' || latng==undefined) {
				// 腾讯定位
				that.getMyLocationTX();
			}else{
				// 地图选点定位
				let latngZH = [];
				latngZH = latng.split(",");
				localStorage.setItem('INlatitude',latngZH[0]);//纬度
				localStorage.setItem('INlongitude',latngZH[1]);//经度
				that.$uerstore.store.commit("addname",addr);//地址状态写入
			}	 
		},
		//定位
		getMyLocationTX() {
			var geolocation = new qq.maps.Geolocation("AZ6BZ-G5WWO-UGOWQ-STA4N-NQG72-2TB4X", "因学教育");
			geolocation.getLocation(this.showPosition, this.showErr,this.addstate);
		},
		showPosition(position) {
			localStorage.setItem('INlatitude',position.lat);//纬度
			localStorage.setItem('INlongitude',position.lng);//经度
			//重新获取内容
			this.$uerstore.store.commit("addname",position.city+position.addr);//地址状态写入
		},
		showErr() {
			console.log("定位失败");
			localStorage.setItem('INlatitude','30.230001');//纬度
			localStorage.setItem('INlongitude','120.213776');//经度
			//重新获取内容
			this.$uerstore.store.commit("addname",'杭州');//地址状态写入
		},
		//根据传入状态页面跳转
		INpagethis(){
			let pageurl = this.$route.query.INpagethisid;
			if (pageurl==1) {
				//已经报名机构页面
				this.$router.push({path:'/JGorder',query:{}});
			};
		}
	},
	watch: {//使用watch 监听$router的变化
	    $route(to, from) {
	      //如果to索引大于from索引,判断为前进状态,反之则为后退状态
	      if(to.meta.index > from.meta.index){
	        //设置动画名称
	        this.transitionName = 'slide-left';
	      }else{
	        this.transitionName = 'slide-right';
	      };
	    }
	  },
	mounted(){
	}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
body,html{
	width: 100%;
	height: 100%;
	background-color: rgba(255,255,255,1);
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 500ms;
  position: absolute;
}
.slide-right-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}



img {
  display: block;
  margin: 0 auto;
}
input[type='file']{
  outline: none;
  margin-top: 20px;
}

#clip_button {
  position: absolute;
  right: 10%;
  bottom: 20px;
  width: 80px;
  height: 40px;
  border:none;
  border-radius: 2px;
  background: #1AAD19;
  color: #fff;
}
#cancel_clip{
  position: absolute;
  left: 10%;
  bottom: 20px;
  width: 80px;
  height: 40px;
  border:none;
  border-radius: 2px;
  color: #fff;
  background:#E64340;
}
#clip_container.container {
  z-index: 99;
  position: fixed;
  padding-top: 60px;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background:rgba(0,0,0,1);
}
#clip_container.container > div{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}
#clip_image {
  max-width: 100%;
}

.cropper-container {
  font-size: 0;
  line-height: 0;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  direction: ltr;
  -ms-touch-action: none;
  touch-action: none
}
.crop_loading , .crop_success {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
}
.crop_loading .crop_content{
  position: absolute;
  top: 50% ;
  left: 50%;
  text-align: center;
  background: #000;
  opacity: 0.9;
  height: 45px;
  width: 100px;
  vertical-align: middle;
  color: #fff;
  padding-top: 20px;
  font-size: 16px;
  -webkit-border-radius:3px;
  border-radius:3px;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}
.crop_loading .crop_content img{
  margin-top: 15px;
  margin-bottom: 10px;
}
.crop_success .crop_success_text{
  position: absolute;
  top: 50% ;
  left: 50%;
  text-align: center;
  background: #000;
  opacity: 0.9;
  width: 120px;
  height: 30px;
  color: #fff;
  line-height: 30px;
  font-size: 16px;
  -webkit-border-radius:3px;
  border-radius:3px;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}
.cropper-container img {
  /* Avoid margin top issue (Occur only when margin-top <= -height) */
  display: block;
  min-width: 0 !important;
  max-width: none !important;
  min-height: 0 !important;
  max-height: none !important;
  width: 100%;
  height: 100%;
  image-orientation: 0deg
}

.cropper-wrap-box,
.cropper-canvas,
.cropper-drag-box,
.cropper-crop-box,
.cropper-modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.cropper-wrap-box {
  overflow: hidden;
}

.cropper-drag-box {
  opacity: 0;
  background-color: #fff;
}

.cropper-modal {
  opacity: .5;
  background-color: #000;
}

.cropper-view-box {
  display: block;
  overflow: hidden;

  width: 100%;
  height: 100%;

  outline: 1px solid #39f;
  outline-color: rgba(51, 153, 255, 0.75);
}

.cropper-dashed {
  position: absolute;

  display: block;

  opacity: .5;
  border: 0 dashed #eee
}

.cropper-dashed.dashed-h {
  top: 33.33333%;
  left: 0;
  width: 100%;
  height: 33.33333%;
  border-top-width: 1px;
  border-bottom-width: 1px
}

.cropper-dashed.dashed-v {
  top: 0;
  left: 33.33333%;
  width: 33.33333%;
  height: 100%;
  border-right-width: 1px;
  border-left-width: 1px
}

.cropper-center {
  position: absolute;
  top: 50%;
  left: 50%;

  display: block;

  width: 0;
  height: 0;

  opacity: .75
}

.cropper-center:before,
.cropper-center:after {
  position: absolute;
  display: block;
  content: ' ';
  background-color: #eee
}

.cropper-center:before {
  top: 0;
  left: -3px;
  width: 7px;
  height: 1px
}

.cropper-center:after {
  top: -3px;
  left: 0;
  width: 1px;
  height: 7px
}

.cropper-face,
.cropper-line,
.cropper-point {
  position: absolute;

  display: block;

  width: 100%;
  height: 100%;

  opacity: .1;
}

.cropper-face {
  top: 0;
  left: 0;

  background-color: #fff;
}

.cropper-line {
  background-color: #39f
}

.cropper-line.line-e {
  top: 0;
  right: -3px;
  width: 5px;
  cursor: e-resize
}

.cropper-line.line-n {
  top: -3px;
  left: 0;
  height: 5px;
  cursor: n-resize
}

.cropper-line.line-w {
  top: 0;
  left: -3px;
  width: 5px;
  cursor: w-resize
}

.cropper-line.line-s {
  bottom: -3px;
  left: 0;
  height: 5px;
  cursor: s-resize
}

.cropper-point {
  width: 5px;
  height: 5px;

  opacity: .75;
  background-color: #39f
}

.cropper-point.point-e {
  top: 50%;
  right: -3px;
  margin-top: -3px;
  cursor: e-resize
}

.cropper-point.point-n {
  top: -3px;
  left: 50%;
  margin-left: -3px;
  cursor: n-resize
}

.cropper-point.point-w {
  top: 50%;
  left: -3px;
  margin-top: -3px;
  cursor: w-resize
}

.cropper-point.point-s {
  bottom: -3px;
  left: 50%;
  margin-left: -3px;
  cursor: s-resize
}

.cropper-point.point-ne {
  top: -3px;
  right: -3px;
  cursor: ne-resize
}

.cropper-point.point-nw {
  top: -3px;
  left: -3px;
  cursor: nw-resize
}

.cropper-point.point-sw {
  bottom: -3px;
  left: -3px;
  cursor: sw-resize
}

.cropper-point.point-se {
  right: -3px;
  bottom: -3px;
  width: 20px;
  height: 20px;
  cursor: se-resize;
  opacity: 1
}

@media (min-width: 768px) {

  .cropper-point.point-se {
    width: 15px;
    height: 15px
  }
}

@media (min-width: 992px) {

  .cropper-point.point-se {
    width: 10px;
    height: 10px
  }
}

@media (min-width: 1200px) {

  .cropper-point.point-se {
    width: 5px;
    height: 5px;
    opacity: .75
  }
}

.cropper-point.point-se:before {
  position: absolute;
  right: -50%;
  bottom: -50%;
  display: block;
  width: 200%;
  height: 200%;
  content: ' ';
  opacity: 0;
  background-color: #39f
}

.cropper-invisible {
  opacity: 0;
}

.cropper-bg {
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQAQMAAAAlPW0iAAAAA3NCSVQICAjb4U/gAAAABlBMVEXMzMz////TjRV2AAAACXBIWXMAAArrAAAK6wGCiw1aAAAAHHRFWHRTb2Z0d2FyZQBBZG9iZSBGaXJld29ya3MgQ1M26LyyjAAAABFJREFUCJlj+M/AgBVhF/0PAH6/D/HkDxOGAAAAAElFTkSuQmCC');
}

.cropper-hide {
  position: absolute;

  display: block;

  width: 0;
  height: 0;
}

.cropper-hidden {
  display: none !important;
}

.cropper-move {
  cursor: move;
}

.cropper-crop {
  cursor: crosshair;
}

.cropper-disabled .cropper-drag-box,
.cropper-disabled .cropper-face,
.cropper-disabled .cropper-line,
.cropper-disabled .cropper-point {
  cursor: not-allowed;
}
</style>



