<template>
	<div class="userdata">
		<!-- 内容 -->
		<van-form @submit="onSubmit">
			<!-- 头图 -->
			<van-divider content-position="left">课程头图</van-divider>
			<img v-show="logoimg!=''" :src="logoimg" alt="" id="imgtrl" style="width: 80px;height: 80px;display: block;">
			<a href="javascript:;" class="file">
				上传课程头图
				<input type="file" accept="image/png,image/jpg,image/jpeg" @change="change($event)">
			</a>
		  <van-field
		    v-model="data.name"
		    name="name"
		    placeholder="课程标题"
		    :rules="[{ required: true, message: '请填写课程标题' }]"
		  />
		  <van-field
		    v-model="data.title"
			name="title"
		    rows="2"
		    autosize
		    type="textarea"
		    maxlength="50"
		    placeholder="课程简介"
		    show-word-limit
			:rules="[{ required: true, message: '请填写简介' }]"
		  />
		  <!-- 分类 -->
		  <van-divider content-position="left">课程分类</van-divider>
		  <van-field name="classification_id" :rules="[{ required: true, message: '请选择分类' }]">
		    <template #input>
		      <van-radio-group v-model="data.classification_id" direction="horizontal">
		        <van-radio style="margin-bottom: 12px;" :name="item.id" checked-color="#c85e55" v-for="(item,index) in KcClass" :key="index">{{item.name}}</van-radio>
		      </van-radio-group>
		    </template>
		  </van-field>
		  
		  <van-field
		    v-model="data.details"
			name="details"
		    rows="6"
		    autosize
		    type="textarea"
		    placeholder="课程内容"
		    show-word-limit
			style="background-color: #f7f7f7;"
		  />
		  
		 <!-- 机构展示图 -->
		 <van-field name="details_img">
		   <template #input>
		     <van-uploader multiple  v-model="data.details_img "   :after-read="afterReadwto" />
		   </template>
		 </van-field>
		  <div style="margin: 16px;">
		    <van-button  color="#c85e55" round block type="info" native-type="submit">
		      发布课程
		    </van-button>
		  </div>
		</van-form>
	</div>
</template>

<script>
	import OSS from 'ali-oss';
	import {
		Toast,Dialog 
	} from 'vant';	
	export default {
	  data() {
	    return {
	      imglogo:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/ren.png',
		  checkbox: false,
		  KcClass:[],      
		  data:{
			 img:[],
			 name: '',
			 title:'',
			 classification_id:'',
			 details:'',
			 details_img :[],
		  },
		  logoimg:''
	    };
	  },
	  methods: {
		  change( event ) {
		      let image = document.getElementById('imgtrl'); //预览对象
		      this.clip(event, {
		        resultObj : image,
		        aspectRatio : 1
		      });
		  },
		  //上传头像
		  logourl(e){
			  let that = this;
			  let dataLGlogo = {
				  imgBase64:e
			  };
			  that.axios.post('v1/getimageDoAliyunOss',that.qs.stringify(dataLGlogo)).then((response)=>{
				  // ...
				if (response.data.code =='1000') {
					console.log(response.data);
					let dataurl = {
						url:response.data.data
					};
					that.data.img.push(dataurl);
					that.logoimg = response.data.data;
				} else{
					Toast('头像上传失败，请重新上传');
					that.logoimg = '';
				}
			  })
		  },
	      onSubmit(values) {
	        console.log('submit', values);
			let that = this;
			that.data.mem_id = this.$uerstore.store.state.INContMechUserId;
			that.data.token = this.$uerstore.store.state._INTOKEN;
			that.axios.post('v1/setContrarianbackCourse ',that.qs.stringify(that.data)).then((response)=>{
			    // ...
				if (response.data.code =='1000') {
					Toast(response.data.msg);
					that.$router.push({path:'/'});
				} else{
					Toast(response.data.msg);
				}
			})
	      },
		  //多图上传
		  afterReadwto(file) {
			let that=this;
			if (file.length>1) {
				//循环赋值多图
				let liats =that.data.details_img.length - file.length;
				//多图
				this.$axios.get('https://zht.insooner.com/ststoken.php').then(function(res){
					for (let i = 0; i < file.length; i++) {
						file[i].status = 'uploading';
						file[i].message = '上传中...';
						that.getOssToken(res.data.Credentials,file[i],liats+i+1);
					};
				}).catch(function (error) {
					console.log(error);							
				});
			} else{
				file.status = 'uploading';
				file.message = '上传中...';
				//单张
				this.$axios.get('https://zht.insooner.com/ststoken.php').then(function(res){
					that.getOssToken(res.data.Credentials,file,that.data.details_img.length );
				}).catch(function (error) {
					console.log(error);
				});
			}
			
		  },
		  // 阿里云上传公共文件单图上传
		  async getOssToken(data,content,urlthis){
		  	let that = this;
		  	// 文件名称
		  	let videoName = 'images/nixingzhe/'+Date.parse(new Date())/1000+content.file.name;
		  	let client = new OSS({
		  		 region: 'oss-cn-hangzhou',
		  		 accessKeyId: data.AccessKeyId,
		  		 accessKeySecret: data.AccessKeySecret,
		  		 stsToken: data.SecurityToken,
		  		 bucket:'yinxuejiaoyu'
		  	 });
		  	 try {
		  		let result = await client.multipartUpload(videoName , content.file, {
		  			progress: async function (p) {//这是上传进度条
					   // console.log(p)
					   if (p==1) {
					   	// 上传成功
					   	content.status = '';
					   };
		  			}
		  		 });
		  		return (that.data.details_img[urlthis-1] = [result].map(curr =>{//on-change回调的参数
		  			return {
		  				name:Date.parse(new Date())/1000+content.file.name,
		  				url: curr.res.requestUrls[0].split('?')[0],
		  				response:curr.res.requestUrls[0].split('?')[0],
		  			}
		  		})[0]);
		  	} catch (e) {
		  		 console.log('失败',e)
		  		if (e.code === 'ConnectionTimeoutError') {
		  			setTimeout(() => {
		  			  content.status = 'failed';
		  			  content.message = '上传失败';
		  			}, 1000);
		  			throw "上传超时!";
		  		}else{
		  			setTimeout(() => {
		  			  content.status = 'failed';
		  			  content.message = '上传失败';
		  			}, 1000);
		  		}
		  	}
		  },
		  //课程分类
		  _KCCLASS(){
			  let that = this;
			  that.axios.post('v1/getContrarianbakcClassification ').then((response)=>{
			      // ...
			  	if (response.data.code =='1000') {
			  		that.KcClass = response.data.data;
					// console.log(that.KcClass);
			  	} else{
			  		Toast(response.data.msg);
			  	}
			  })
		  }
	    },
		mounted() {
			this._KCCLASS();
		}
	}
</script>

<style scoped>
	.userdata{
		width: 100%;
	}
	.userdatalogo{
		width: 40px;
		height: 40px;
		overflow: hidden;
		border-radius: 50%;
		margin: 20px auto 10px auto;
	}
	.userdatalogo img{
		width: 100%;
		display: block;
	}
	.file {
	    position: relative;
	    display: inline-block;
	    background: #D0EEFF;
	    border: 1px solid #99D3F5;
	    border-radius: 4px;
	    padding: 4px 12px;
	    overflow: hidden;
	    color: #1E88C7;
	    text-decoration: none;
	    text-indent: 0;
	    line-height: 20px;
		margin-top: 20px;
	}
	.file input {
	    position: absolute;
	    right: 0;
	    top: 0;
	    opacity: 0;
		margin: 0;
	}
	.file:hover {
	    background: #AADFFD;
	    border-color: #78C3F3;
	    color: #004974;
	    text-decoration: none;
	}
</style>



